<template>
    <section class="layout" id="layout">

        <div class="news-row" v-for="row in rowedNews">
            <img v-for="item in row"
                 v-bind:src="item.img" v-bind:style="{'max-height': height + 'px'}" v-on:click="showImg(item)"/>
        </div>

    </section>
</template>

<script>
    import {EventBus, Events} from "../common/eventBus";

    export default {
        name: "Home",

        data: () => {
            return {
                news: [],

                height: 0,
            }
        },

        methods: {
            showImg(item) {
                EventBus.$emit(Events.showImagePreview, {img: item.img, height: 90})
            },
        },

        computed: {

            rowedNews: function () {
                let res = [],
                    rowedNews = [];

                for (let i = 0; i < this.news.length;i++) {
                    if (rowedNews.length === 2) {
                        res.push(rowedNews);
                        rowedNews = [];
                    }

                    rowedNews.push(this.news[i]);

                    if (i === (this.news.length - 1)) {
                        res.push(rowedNews)
                    }
                }

                return res;
            },

        },

        mounted() {
            this.height = document.documentElement.clientHeight - (200 + 10);

            for (let i = 0;i < 5; i++) {
                this.news.push({img: "/0001.jpg"})
            }
        },
    }
</script>

<style lang="sass" scoped>

    .layout
        width: 90%
        max-width: 1200px
        margin: auto

        .news-row
            display: flex
            justify-content: space-evenly

            img
                cursor: pointer
                height: auto
                width: auto
                max-width: 50%
                align-self: center

    .title
        text-align: center

    .placeholder
        display: block
        margin-top: 1.5rem
        margin-bottom: 1.5rem
        height: 300px
        background: url(/0001.jpg) center
</style>